/* My Portfolio Starts */
.portfolio-section {
    display: flex;
    background: linear-gradient(90.21deg, #AA367C -5.91%, #d70c8d 111.58%);
    padding: 112px 85px;
    flex-direction: column;
    align-items: flex-start;
    gap: 40px;
  }
  .portfolio-container-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  }
  .portfolio-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 21.3px;
  }
  .portfolio-section-container {
    display: grid;
    align-items: center;
    justify-content: center;
    gap: 42.6px;
    width: 100%;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); /* Adjust the minimum and maximum width of each grid item */
  }
  .portfolio-section-img {
    border-radius: 8px;
  }
  .portfolio-section-img > img {
    width: 100%;
  }
  .portfolio-section-card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    flex: 1 0 0;
    background: #fff;
    box-shadow: 0 0 32px 0 rgba(0, 0, 0, 0.15);
    cursor: pointer;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
  }
  .portfolio-section-card-content {
    display: flex;
    padding: 32px;
    flex-direction: column;
    align-items: flex-start;
    gap: 32px;
    align-self: stretch;
  }
  .portfolio-section-title {
    color: var(--heading-color);
  }
  .portfolio-link {
    text-decoration: none;
    display: flex;
    gap: 16px;
    border-bottom: 1.33px solid var(--primary);
    padding-bottom: 10.6px;
    font-weight: 600;
  }
  .portfolio-section-card:hover path {
    stroke: #006b6a;
  }
  /* Portfolio Ends */