/* Navbar Style Start */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 85.333px;
  background: var(--white);
  box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.navbar-items > ul {
  list-style: none;
  display: flex;
  align-items: flex-start;
  gap: 22.667px;
  text-decoration: none;
}
.navbar-items ul > li > a {
  text-decoration: none;
}
/* Navbar Content */
.navbar-content {
  color: var(--darkblue);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  cursor: pointer;
}
.navbar-active-content {
  color: var(--primary);
}
/* Navbar Styler Ends */